import { Component, OnInit } from '@angular/core';
import {NoticiaService} from '../noticia.service';

@Component({
  selector: 'app-form-noticia',
  templateUrl: './form-noticia.component.html',
  styleUrls: ['./form-noticia.component.css']
})
export class FormNoticiaComponent implements OnInit {

  NoticiaInfo: any = {};
  response: any;
  error: any;
  noticias: any;

  constructor(private noticiaService: NoticiaService) { }

  ngOnInit() {
  }

  getAllNoticia(){
    this.noticiaService.getAllNoticia()
      .subscribe( noticias => {
        this.noticias = noticias;
      });
  }

  createNoticia(){
    console.log(this.NoticiaInfo);
    this.noticiaService.createNoticia(this.NoticiaInfo)
      .subscribe( noticia => {
        this.error = null;
        this.response = noticia;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

}
