import { Component, OnInit } from '@angular/core';
import {TrabajoService} from '../trabajo.service';

@Component({
  selector: 'app-form-trabajo',
  templateUrl: './form-trabajo.component.html',
  styleUrls: ['./form-trabajo.component.css']
})
export class FormTrabajoComponent implements OnInit {


  PostulacionInfo: any = {};
  response: any;
  error: any;
  postulaciones: any;

  constructor(private trabajoService: TrabajoService) { }

  ngOnInit() {
  }

  getAllPostulacion(){
    this.trabajoService.getAllPostulacion()
      .subscribe( posts => {
        this.postulaciones = posts;
      });
  }

  createPostulacion(){

    this.trabajoService.createPostulacion(this.PostulacionInfo)
      .subscribe( post => {
        this.error = null;
        this.response = post;
      }, err => {
        console.log(err);
        this.error = err;
      });
  }

}
